import { getProjectScoped, postProjectScoped } from '../projectApi'
import { axiosGeneration } from '@/lib/axios'

jest.mock('@/lib/axios', () => ({ axiosGeneration: { get: jest.fn(), post: jest.fn() } }))

describe('projectApi wrappers', () => {
  beforeEach(() => {
    (axiosGeneration.get as any).mockReset()
    (axiosGeneration.post as any).mockReset()
  })

  it('getProjectScoped builds correct URL', async () => {
    ;(axiosGeneration.get as any).mockResolvedValue({ data: {} })
    await getProjectScoped('flashcard-sets/', '1')
    expect(axiosGeneration.get).toHaveBeenCalled()
    const url = (axiosGeneration.get as any).mock.calls[0][0]
    expect(url).toMatch(/^projects\//)
  })

  it('postProjectScoped builds correct URL', async () => {
    ;(axiosGeneration.post as any).mockResolvedValue({ data: {} })
    await postProjectScoped('flashcards/generate', '1', { a: 1 })
    expect(axiosGeneration.post).toHaveBeenCalled()
    const url = (axiosGeneration.post as any).mock.calls[0][0]
    expect(url).toMatch(/^projects\//)
  })
})
